        "title": "Authorization and Permission in Linux OS",
        "introduction": "In this tutorial, learn to control file access through correct use of file and directory permissions and ownerships. Learn to:\r\n\r\nManage access permissions on both regular and special files as well as directories\r\nMaintain security using access modes such as suid, sgid, and the sticky bit\r\nChange the file creation mask\r\nGrant file access to group members\r\nThis tutorial helps you prepare for Objective 104.5 in Topic 104 of the Linux Server Professional (LPIC-1) exam 101. The objective has a weight of 3.",
