        "body": "What groups am I in\r\nSimilarly, you can find out what groups you are in by using the groups command. You can find out both user and group information using the id command. Add a user ID parameter to either groups or id to see information for that user ID instead of the current user ID. Listing 2 shows some examples. Note that without a user ID, the id command will also display SELinux context as well as basic ID information.\r\n\r\nAdding permissions\r\nSuppose you create a “Hello world” shell script. When you first create the script, it will usually not be executable. Use the chmod command with the +x option to add the execute permissions as shown in Listing 5.\r\n\r\nListing 5. Creating an executable shell script",
